        <p>Antes de nada, que no se enfaden los de sistemas computacionales, redes, hardware, bases de datos y dems tribus, sabemos que el vasto territoro de lo tecnolgco abarca innumerables reas y todas son importantes, no se trata aqu de establecer un ranquing de importancia ni de ofrecer un riguroso estudio del panorama actual.</p>
        <p><strong>Simplemente veremos desde la ptica TODH una aplicacin web armada desde el frontend con los 4 elementos que considero bsicos: javascript, css, html y json.</strong> Cada uno de ellos tiene una funcin, unas cualidades, una energa que unida constituye la inmensa mayora de sitios web. Tratar de que veamos cmo hasta la tecnologa sigue unas pautas, un orden, que podemos extrapolar anlogamente a cualquier otro mbito, desde lo micro hasta lo macro.</p>

        <CodeBlock language="js" code="{code_javascript}" header="Fuego"/>
        <p>El lenguaje de programacn es el que decide cmo se van a comportar todos. Decide y determina cules sern las funciones o acciones que se van a llevar a cabo. En un sitio web se cumple su voluntad s o s.</p>
        <p>No es fcil de aprender y su poder no tiene lmites. Es el fuego, es el pensamiento, la voluntad, el motor que mueve las cosas, las procesa. Su arquetipo es Marte, el guerrero y el rey que hace lo que quiere.</p>
        
        <CodeBlock language="css" code="{code_scss}" header="Agua"/>
        <p>La hoja de estilos se encarga del aspecto visual, el tono, la consistencia, la armona, el ritmo, la armona, las proporciones, la empata con el usuario, lo cognitivo, la interfaz grafica, el sabor. El CSS dota de identidad a los distintos elementos de la App.</p>
        <p>No por casualidad su elemento es el agua y el nmero dos, porque media entre lo de dentro y lo de afuera, entre el observador y lo observado.</p>
        
        <CodeBlock language="html" code="{code_markup}" header="Tierra"/>
        <p>El <code>Html</code> nos dar la base sobre la que se edifica todo. Es la estructura de nuestro sitio. Es el elemento que da cuerpo, que baja, cristaliza y posibilita las ideas. Es el sentido de realidad tan necesario. Al <code>Html</code> le interesa la practicidad, la accesibilidad. Es el lienzo donde todo ocurre y se manifiesta. Es el organizador, el soporte.</p>

        <CodeBlock language="json" code="{code_json}" header="Aire"/>
        <p>Y llegamos al contenido, al mensaje, el vnculo con el otro. Es el reino del lenguaje. Uno de los formatos que ms se usa es JSON, un standar para el intercambio de informacin. Innumerables webs se conectan a servicios API Rest que consumen datos. Tiene que ver con el arquetipo del mensajero que transmite.</p>

